.auth {
    position: fixed;
    display: flex;
    justify-content: center;
    top: 0;
    left: 0;
    z-index: 3;

    width: 100vw;
    height: 110vh;

    background-color: rgba(#000000, 0.3);
    backdrop-filter: blur(10px);
    &__container {
        border-radius: 8px;
        padding: 28px;
        background-color: var(--background);
        position: fixed;
        top: 50%;
        left: 50%;
        transform: translate(-50%, -50%);
        width: 85%;
        max-width: 350px;
        height: auto;
        transition: height 0.5s ease-in-out;
    }
    &__header {
        display: flex;
        justify-content: space-between;
        color: var(--primary);
    }
    &__navButtons {
        display: flex;
        align-items: center;
    }
    &__icon {
        cursor: pointer;
        margin-left: 8px;
        display: flex;
        align-items: center;
    }
    &__form {
        display: flex;
        justify-content: center;
        flex-direction: column;
    }

    &__success {
      color: var(--primary);
    }
    
    &__formSection {
      margin-top: 16px;  
      margin-bottom: 20px;
    }
    &__formLabel {
        display: block;
        font-size: 12px;
        font-style: normal;
        font-weight: 600;
        line-height: normal;
        margin-bottom: 8px;
        color: var(--primary)
    }
    &__formCall {
        display: block;
        font-size: 12px;
        font-style: normal;
        font-weight: 600;
        line-height: normal;
        margin-top: 16px;
    }
    &__formCall {
        display: block;
        font-size: 12px;
        font-style: normal;
        font-weight: 600;
        line-height: normal;
        margin-top: 16px;
    }
    &__formInput {
        width: 100%;
        border-radius: 4px;
        height: 30px;
        outline: 1px solid black;
        border: none;
        background-color: #fff;
        padding: 8px; 
      }
    &__formInputFile {
        font-size: 12px;
        font-style: normal;
        font-weight: 600;
        line-height: normal;
    }
    &__error {
        color: red;
        font-size: 12px;
        font-style: normal;
        font-weight: 600;
        line-height: normal;
    }
    &__formButton {
        margin-top: 16px;
        color: white;
        background: var(--accent);
        border: 0 solid;
        border-radius: 8px;
        display: flex;
        height: 48px;
        justify-content: center;
        align-items: center;
        flex-shrink: 0;
        font-size: 14px;
        font-style: normal;
        font-weight: 700;
        line-height: 21px;
        transition: 0.3s;
        &--light {
            color: var(--accent);
            border: 1px solid var(--accent);
            background: white;
            &:hover {
                color: white;
                background: var(--accent);
                box-shadow: 0px 3px 13px 0px rgba(23, 32, 49, 0.4);
            }
        }
        &:disabled {
            background-color: #FAFBFC;
            border: 1px solid #e2e6e9;
            color: #e2e6e9;
            &:hover {
                box-shadow: none;
            }
            &:active {
                color: #e2e6e9;
                background-color: #FAFBFC;
                border: 1px solid #e2e6e9;
                }
        }
        &:hover {
            box-shadow: 0px 3px 13px 0px rgba(23, 32, 49, 0.4);
        }
        &:active {
        color: var(--accent);
        background-color: #fff;
        border: 1px solid #e2e6e9;
        }
    }
    .auth__error {
        height: 15px;
        font-size: 12px;
        font-style: normal;
        font-weight: 550;
        line-height: normal;
    }
}